import company from "../controllers/company";
export default (app) => {

    /* Routes for add company */
    app.route("/company/addCompany").post(company.addCompany);

    app.route("/company/getAllCompany").get(company.getAllCompany);

    app.route("/company/updateCompany").put(company.updateCompany);

    app.route("/company/deleteCompany/:idcompany").delete(company.deleteCompany);

    return app;
};